import React from 'react';
import { string, shape, bool } from 'prop-types';

import { SignupForm } from '../../../components/SignupForm';
import { TermsLink } from '../../../components/TermsLink';

import styles from './MainBlock.module.scss';

const MainBlock = ({ params }) => (
  <div className={styles.mainBlock__container}>
    <h1 dangerouslySetInnerHTML={{ __html: params && params.title }} />

    <p
      className={`copytext-large ${styles.mainBlock__description}`}
      dangerouslySetInnerHTML={{ __html: params && params.description }}
    />

    <SignupForm
      buttonText={params && params.buttonText}
      inputPlaceholder={params && params.inputPlaceholder}
    />
    {/* Make sure you don't load a component that could break in Netlify. Check if in preview mode */}
    {params && params.previewMode ? null : params && <TermsLink />}
  </div>
);

MainBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    inputPlaceholder: string,
    buttonText: string
  }),
  previewing: bool
};

MainBlock.defaultProps = {
  params: {},
  previewing: false
};

export default MainBlock;



